@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
:root {
  --san-serif: -apple-system, BlinkMacSystemFont, sans-serif;
  --fontSize: 16px;
  --lineHeight: calc(var(--fontSize) * 1.5);
  --columnGap: calc(var(--lineHeight) * 2);
  --rowGap: calc(var(--lineHeight) * 2);

  --background: rgb(255, 255, 255);
  --PrimaryCopy: rgb(10, 10, 10);
  --SecondairyCopy: rgb(117, 117, 117);
  --gray-400: rgb(117, 117, 117);

  --pink: rgb(255, 0, 64);

  --serif: ui-serif;
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: rgb(20, 21, 24);
    --PrimaryCopy: rgb(240, 240, 240);
    --SecondairyCopy: rgb(150, 150, 150);
  }
}

@supports (color: color(display-p3 1 1 1)) {
  :root {
    --pink: color(display-p3 1 0 0.2823 / 1);
  }
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  background: rgb(255, 255, 255);
  background: var(--background);
  color: rgb(117, 117, 117);
  color: var(--SecondairyCopy);

  font-family: -apple-system, BlinkMacSystemFont, sans-serif;

  font-family: var(--san-serif);
  font-size: 16px;
  font-size: var(--fontSize);
  font-weight: 300;
  line-height: calc(16px * 1.5);
  line-height: var(--lineHeight);

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-weight: normal;
  word-wrap: break-word;
  -webkit-font-kerning: normal;
          font-kerning: normal;
  -ms-font-feature-settings: "kern", "liga", "clig", "calt";
  font-feature-settings: "kern", "liga", "clig", "calt", "kern";
}

#root {
  -webkit-justify-content: center;
          justify-content: center;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
  width: 100vw;
  min-height: 100vh;
  max-width: 75rem;
  display: grid;
  margin: 2rem auto;
}

.App {
  padding: 0 1rem;
}

nav {
  margin: 4rem 0;
  text-align: right;

  display: -webkit-flex;

  display: flex;
  line-height: calc(16px * 1.5);
  line-height: var(--lineHeight);

  -webkit-align-content: center;

          align-content: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

nav a {
  text-decoration: none;
  transition: color 0.3s;
  color: rgb(117, 117, 117);
  color: var(--SecondairyCopy);
  margin: 0 0 0 1rem;
}

nav a.selected,
nav a:hover {
  color: rgb(10, 10, 10);
  color: var(--PrimaryCopy);
}

nav .logo {
  font-weight: 600;
  letter-spacing: -0.125px;
  margin: 0 auto 0 0;
}

/* !footer */
footer {
  margin: 10rem 0 0 0;
  padding: 0.5rem 0;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  line-height: calc(16px * 1.5);
  line-height: var(--lineHeight);
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
}

footer a {
  text-decoration: none;
  transition: color 0.3s;
  color: rgb(117, 117, 117);
  color: var(--SecondairyCopy);
  margin: 0 1rem 0 0;
}

/* !content */

picture,
img {
  display: block;
  height: auto;
  max-width: 100%;
  margin: 0;
  background: rgb(117, 117, 117);
  background: var(--SecondairyCopy);
}

/* !breakpoints */

@media (max-width: 768px) {
  #root {
    max-width: 48rem;
  }
}

@media (max-width: 576px) {
  #root {
    max-width: 36rem;
  }

  nav {
    margin: 1rem 0 4rem 0;
    text-align: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    display: -webkit-flex;
    display: flex;

    line-height: calc(16px * 1.5);

    line-height: var(--lineHeight);

    -webkit-align-content: center;

            align-content: center;
    -webkit-justify-content: center;
            justify-content: center;
  }

  nav a {
    margin: 0;
  }

  nav .logo {
    -webkit-flex: 0 0 100%;
            flex: 0 0 100%;
    margin: 1rem;
    text-align: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: center;
            justify-content: center;
  }
}

